import React, { useEffect } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';

import { isExpired, decodeToken } from "react-jwt";


import Home from './components/Home';
import Styles from './components/Styles';
import StyleToText from './components/StyleToText';
import Models from './components/Models';
import Model from './components/Model';
import TextToImage from './components/TextToImage';
import ImageToImage from './components/ImageToImage';
import ImageToImageHook from './components/ImageToImageHook';
import NotFound from './components/NotFound';

import 'bootstrap';

import './App.css';

import $ from 'jquery';

function App() {
  return (
    <Routes>

      <Route path="/" element={<Home />} />
      <Route path="/styles" element={<Styles />} />
      <Route path="/style/:name/:index" element={<StyleToText />} />
      <Route path="/models" element={<Models />} />
      <Route path="/model/:category" element={<Model />} />
      <Route path="/text-to-image" element={<TextToImage />} />
      <Route path="/image-to-image" element={<ImageToImage />} />
      <Route path="/image-to-image/:category/:index" element={<ImageToImageHook />} />

      <Route path="*" element={<NotFound />} />

    </Routes>
  );

}

export default App;
