import React, { useState, useEffect } from 'react';
import $ from 'jquery';
import Navbar from './Navbar';
import Footer from './Footer';
import Score from './Score';
import 'react-bootstrap-range-slider/dist/react-bootstrap-range-slider.css';
import RangeSlider from 'react-bootstrap-range-slider';

import Swal from 'sweetalert2';

import 'bootstrap';

import { model_list, model_list_other } from './models/list';


function ImageToImage() {

    const [styles, setStyles] = useState(1);

    const [imageWidth, setImageWidth] = useState(512);
    const [imageHeight, setImageHeight] = useState(512);
    const [guidianceScale, setGuidianceScale] = useState(7.5);
    const [steps, setSteps] = useState(30);
    const [seed, setSeed] = useState(1);
    const [samples, setSamples] = useState(1);
    const [upscale, setUpscale] = useState(1);
    const [controlnet_conditioning_scale, setControlnet_conditioning_scale] = useState(0.7);

    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);

    const [viewModels, setViewModels] = useState(1);

    const [modelCategory, setModelCategory] = useState(1);
    const [elementModel, setElementModel] = useState(1);

    const handlePageChange = (page) => {
        setCurrentPage(page);
        view_models(page);
        $('.models-list').removeClass('active-model');
    };

    const renderPagination = () => {
        const pagesToShow = [];
        const pagesLimit = totalPages < 7 ? totalPages : 7;
        let startPage = 1;
        if (currentPage > pagesLimit / 2) {
            startPage = currentPage - Math.floor(pagesLimit / 2);
        }

        let endPage = startPage + pagesLimit - 1;
        if (endPage > totalPages) {
            endPage = totalPages;
            startPage = endPage - pagesLimit + 1;
        }

        for (let i = startPage; i <= endPage; i++) {
            pagesToShow.push(
                <li
                    key={i}
                    className={`page-item ${currentPage === i ? 'active' : ''}`}
                    onClick={() => handlePageChange(i)}
                >
                    <a className="page-link">
                        {i}
                    </a>
                </li>
            );
        }

        return pagesToShow;
    };

    const modelAvtive = function (model_id, model_category, src, id) {

        $('#btn-model').attr({
            'model_id': model_id,
            'data-id': id,
            'data-src': src
        })

        $('.models-list').removeClass('active-model');
        $('#' + model_id).addClass('active-model');

    }

    function createBlock(num) {
        let el = [];
        for (let i = 0; i < num; i++) {
            el.push(
                <div key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">

                    <div style={{ width: '100%', height: '175px', backgroundColor: 'rgba(44, 51, 73, 0.3)', borderTopLeftRadius: '8px', borderTopRightRadius: '8px' }}>

                    </div>
                    <div className="text-overflow">
                        <div style={{ width: '100%', height: '24px', backgroundColor: 'rgba(44, 51, 73, 0.2)', borderBottomLeftRadius: '8px', borderBottomRightRadius: '8px' }}></div>
                    </div>

                </div>
            )
        }
        return el;
    }


    const view_models = (page) => {

        setViewModels(createBlock(8));

        model_list(modelCategory, page, 8, function (model, pages) {

            let numberPage = (pages / 12);

            if (Number.isInteger(numberPage)) {
                setTotalPages(numberPage);
            } else {
                setTotalPages(parseInt(numberPage) + 1);
            }

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray" onClick={() => modelAvtive(model[i].model_id, model[i].model_category,
                        model[i].screenshots, elementModel)}>
                        <div>
                            <img className="img-w100" src={model[i].screenshots} />
                        </div>
                        <div className="text-overflow">
                            <span className="p-2 font-medium">{model[i].model_name}</span>
                        </div>
                    </div>
                )
            }
            setTimeout(() => {
                setViewModels(el);
            }, 1000);
        });
    };


    useEffect(() => {

        var init_upload = document.createElement('input');
        init_upload.setAttribute('type', 'file');
        init_upload.setAttribute('accept', '.jpg,.jpeg,.png');

        init_upload.addEventListener('change', function (event) {
            var selectedFile = event.target.files[0];
            if (selectedFile) {
                var reader = new FileReader();
                reader.onload = function (event) {
                    var base64String = event.target.result;
                    $('#init_image img.pic').attr('src', base64String).removeClass('d-none').addClass('d-block');
                    $('#init_image i').removeClass('d-block').addClass('d-none');
                    $('#init_image .svg-close').removeClass('d-none').addClass('d-block');
                };

                reader.readAsDataURL(selectedFile);
            }
        });

        var control_upload = document.createElement('input');
        control_upload.setAttribute('type', 'file');
        control_upload.setAttribute('accept', '.jpg,.jpeg,.png');

        control_upload.addEventListener('change', function (event) {
            var selectedFile = event.target.files[0];
            if (selectedFile) {
                var reader = new FileReader();
                reader.onload = function (event) {
                    var base64String = event.target.result;
                    $('#control_image img.pic').attr('src', base64String).removeClass('d-none').addClass('d-block');
                    $('#control_image i').removeClass('d-block').addClass('d-none');
                    $('#control_image .svg-close').removeClass('d-none').addClass('d-block');
                };

                reader.readAsDataURL(selectedFile);
            }
        });

        setStyles(createBlock(4));

        model_list_other('styles', Math.floor(Math.random() * 102), 4, function (model, pages) {

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={window.location.origin + '/style/' + model[i].model_name + '/' + pages[i]}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }
            setTimeout(() => {
                setStyles(el);
            }, 1000);
        });

        function getModels(modelCategory, elementModel) {

            setViewModels(createBlock(8));

            model_list(modelCategory, 1, 8, function (model, pages) {

                let numberPage = (pages / 12);

                if (Number.isInteger(numberPage)) {
                    setTotalPages(numberPage);
                } else {
                    setTotalPages(parseInt(numberPage) + 1);
                }

                let title = '';
                let el = [];
                for (let i in model) {
                    if (!title) {
                        title = model[i].model_category;
                    }
                    el.push(
                        <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray" onClick={() => modelAvtive(model[i].model_id, model[i].model_category,
                            model[i].screenshots, elementModel)}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </div>
                    )
                }

                setTimeout(() => {
                    setViewModels(el);
                }, 1000);

                if (title == 'stable_diffusion') {
                    $('#modal-models .modal-title').text('Stable Diffusion');
                }
                if (title == 'stable_diffusion_xl') {
                    $('#modal-models .modal-title').text('Stable Diffusion XL');
                }
                if (title == 'embeddings') {
                    $('#modal-models .modal-title').text('Embeddings');
                }
                if (title == 'lora') {
                    $('#modal-models .modal-title').text('LORA');
                }
                if (title == 'controlnet') {
                    $('#modal-models .modal-title').text('ControlNet');
                }
                if (title == 'controlnet_type') {
                    $('#modal-models .modal-title').text('ControlNet Type');
                }
            });
        }

        function processing(form, callback) {
            $.post('https://artpix.000webhostapp.com/process.php', form, function (res) {

                let result = JSON.parse(res);

                if (result.status === 'success') {
                    if (callback) {
                        callback(result);
                    }
                } else if (result.status === 'processing') {
                    setTimeout(function () {
                        processing(form, callback);
                    }, 10000);
                } else if (result.status === 'error') {
                    if (callback) {
                        callback(false);
                    }
                }
            });
        }

        $('#btn-model').on('click', function () {
            let element = $('[element-model="' + $(this).attr('data-id') + '"]');
            element.find('i').removeClass('d-block').addClass('d-none');
            element.find('.pic').attr('src', $(this).attr('data-src')).removeClass('d-none');
            element.find('.svg-close').removeClass('d-none');
            $(element.parent()[0]).find('.box-model-name span').text($(this).attr('model_id')).css('visibility', 'visible');
        });

        $('.box-model').on('click', function (e) {

            if (e.target.className != 'svg-close') {
                let models = $(this).attr('data-model');
                let element_model = $(this).attr('element-model');

                setElementModel(element_model);

                setModelCategory(models);
                getModels(models, element_model);

                $('#modal-models').modal('show');
            } else {
                $(this).find('i').removeClass('d-none').addClass('d-block');
                $(this).find('.pic').addClass('d-none');
                $($(this).parent()[0]).find('.box-model-name span').text('Null').css('visibility', 'hidden');
                $(this).find('.svg-close').addClass('d-none');
            }
        });

        $('.box-file-init').on('click', function (e) {
            if (e.target.tagName != 'IMG') {
                init_upload.click();
            } else {
                $(this).find('i').removeClass('d-none').addClass('d-block');
                $(this).find('.pic').removeClass('d-block').addClass('d-none').attr('src', '');
                $(this).find('.svg-close').removeClass('d-block').addClass('d-none');
            }
        });

        $('.box-file-control').on('click', function (e) {
            if (e.target.tagName != 'IMG') {
                control_upload.click();
            } else {
                $(this).find('i').removeClass('d-none').addClass('d-block');
                $(this).find('.pic').removeClass('d-block').addClass('d-none').attr('src', '');
                $(this).find('.svg-close').removeClass('d-block').addClass('d-none');
            }
        });

        var count = 0;
        var interval;

        var lastFrameTime = 0;
        var delay = 100; //  ()

        function updateCounter(timestamp) {
            if (!lastFrameTime || timestamp - lastFrameTime >= delay) {
                $('#loadding-count').text(count.toFixed(2));
                count += 0.01;
                lastFrameTime = timestamp;
            }

            interval = requestAnimationFrame(updateCounter);
        }

        $('#btn-generate').on('click', function () {

            let self = this;
            $(self).prop('disabled', true);
            $('#loadding').removeClass('v-none').addClass('v-block');

            let prompt = $('#prompt').val() ? $('#prompt').val() : 'null';
            let negative_prompt = $('#negative-prompt').val() ? $('#negative-prompt').val() : 'null';
            let init_image = $('#init_image img.pic').attr('src');
            let control_image = $('#control_image img.pic').attr('src');
            let image_width = $('#image-width').val();
            let image_height = $('#image-height').val();
            let guidiance_scale = $('#guidiance-scale').val();
            let steps = $('#steps').val();
            let scheduler = $('#select-Scheduler').val();
            let seed = $('#seed').val();

            let model_id = $('#base-model .box-model-name span').text() == 'Null' ? 0 : $('#base-model .box-model-name span').text();
            let lora_model = [];
            let embeddings_model = $('#embeddings-model .box-model-name span').text() == 'Null' ? 0 : $('#embeddings-model .box-model-name span').text();
            let controlnet_model = [];
            let controlnet_type = $('#controlnet-type-model .box-model-name span').text() == 'Null' ? 0 : $('#controlnet-type-model .box-model-name span').text();

            let samples = 1;//$('#samples').val();
            let upscale = $('#upscale').val();
            let enhance_prompt = $('#enhance_prompt')[0].checked;
            let use_karras_sigmas = $('#use_karras_sigmas')[0].checked;
            let tomesd = $('#tomesd')[0].checked;
            let self_attention = $('#self_attention')[0].checked;
            let highres_fix = $('#highres_fix')[0].checked;

            let auto_hint = $('#auto_hint')[0].checked;
            let guess_mode = $('#guess_mode')[0].checked;
            let controlnet_conditioning_scale = $('#controlnet_conditioning_scale').val()

            $('#lora-model .box-model-name span').map(function () {
                if ($(this).text() != 'Null') {
                    lora_model.push($(this).text());
                }
            });
            lora_model = lora_model.join(',');

            $('#controlnet-model .box-model-name span').map(function () {
                if ($(this).text() != 'Null') {
                    controlnet_model.push($(this).text());
                }
            });
            controlnet_model = controlnet_model.join(',');

            let form = {
                "prompt": prompt,
                "negative_prompt": negative_prompt,
                "width": image_width,
                "height": image_height,
                "num_inference_steps": steps,
                "scheduler": scheduler,
                "seed": seed,
                "guidance_scale": guidiance_scale,
                "multi_lingual": "no",
                "samples": samples,
                "upscale": upscale,
                "enhance_prompt": enhance_prompt ? "yes" : "no",
                "use_karras_sigmas": use_karras_sigmas ? "yes" : "no",
                "tomesd": tomesd ? "yes" : "no",
                "panorama": "no",
                "self_attention": self_attention ? "yes" : "no",
                "highres_fix": highres_fix ? "yes" : "no",
                "auto_hint": auto_hint ? "yes" : "no",
                "guess_mode": guess_mode ? "yes" : "no",
                "controlnet_conditioning_scale": controlnet_conditioning_scale
            }

            if (init_image) {
                form['init_image'] = init_image;
            }

            if (control_image) {
                form['control_image'] = control_image;
            }

            if (model_id) {
                form['model_id'] = model_id;
            }

            if (lora_model) {
                form['lora_model'] = lora_model;
            }

            if (embeddings_model) {
                form['embeddings_model'] = embeddings_model;
            }

            if (controlnet_model) {
                form['controlnet_model'] = controlnet_model;
            }

            if (controlnet_type) {
                form['controlnet_type'] = controlnet_type;
            }

            interval = requestAnimationFrame(updateCounter);

            const Toast = Swal.mixin({
                toast: true,
                position: "top-end",
                showConfirmButton: false,
                timer: 5000,
                timerProgressBar: true,
                didOpen: (toast) => {
                    toast.onmouseenter = Swal.stopTimer;
                    toast.onmouseleave = Swal.resumeTimer;
                }
            });
            Toast.fire({
                title: "Start processing."
            });

            $.post('https://artpix.000webhostapp.com/img2img.php', form, function (res) {

                let result = JSON.parse(res);

                if (result.status === 'success') {
                    for (let i in result.output) {

                        let html = '<div class="p-2 col-md-3 col-sm-6 col-6">' +
                            '<img class="preview-pic img-w100" src="' + result.output[i] + '"/>' +
                            '</div>';

                        $('#result').prepend(html);
                    }

                    $(self).prop('disabled', false);
                    $('#loadding').removeClass('v-block').addClass('v-none');
                    cancelAnimationFrame(interval);
                    count = 0;

                    const Toast = Swal.mixin({
                        toast: true,
                        position: "top-end",
                        showConfirmButton: false,
                        timer: 5000,
                        timerProgressBar: true,
                        didOpen: (toast) => {
                            toast.onmouseenter = Swal.stopTimer;
                            toast.onmouseleave = Swal.resumeTimer;
                        }
                    });
                    Toast.fire({
                        icon: "success",
                        title: "Generate successfully."
                    });

                } else if (result.status === 'processing') {

                    const Toast = Swal.mixin({
                        toast: true,
                        position: "top-end",
                        showConfirmButton: false,
                        timer: 5000,
                        timerProgressBar: true,
                        didOpen: (toast) => {
                            toast.onmouseenter = Swal.stopTimer;
                            toast.onmouseleave = Swal.resumeTimer;
                        }
                    });
                    Toast.fire({
                        icon: "warning",
                        title: "Processing, please wait a time"
                    });

                    let _form = {
                        "key": result.key,
                        "id": result.id,
                    }

                    processing(_form, function (result_process) {

                        if (result_process?.status === 'success') {
                            for (let i in result_process.output) {

                                var img = new Image();
                                img.src = result_process.output[i];

                                img.onload = function () {
                                    let html = '<div class="p-2 col-md-3 col-sm-6 col-6">' +
                                        '<img class="preview-pic img-w100" src="' + result_process.output[i] + '" />' +
                                        '</div>';

                                    $('#result').prepend(html);
                                };

                                img.onerror = function () {
                                    setTimeout(() => {
                                        let html = '<div class="p-2 col-md-3 col-sm-6 col-6">' +
                                            '<img class="preview-pic img-w100" src="' + result_process.output[i] + '?v=' + (new Date().getTime()) + '" />' +
                                            '</div>';
                                        $('#result').prepend(html);
                                    }, 5000);
                                };


                            }

                            $(self).prop('disabled', false);
                            $('#loadding').removeClass('v-block').addClass('v-none');
                            cancelAnimationFrame(interval);
                            count = 0;

                            const Toast = Swal.mixin({
                                toast: true,
                                position: "top-end",
                                showConfirmButton: false,
                                timer: 5000,
                                timerProgressBar: true,
                                didOpen: (toast) => {
                                    toast.onmouseenter = Swal.stopTimer;
                                    toast.onmouseleave = Swal.resumeTimer;
                                }
                            });
                            Toast.fire({
                                icon: "success",
                                title: "Generate successfully."
                            });
                        } else {
                            $(self).prop('disabled', false);
                            $('#loadding').removeClass('v-block').addClass('v-none');
                            cancelAnimationFrame(interval);
                            count = 0;

                            const Toast = Swal.mixin({
                                toast: true,
                                position: "top-end",
                                showConfirmButton: false,
                                timer: 5000,
                                timerProgressBar: true,
                                didOpen: (toast) => {
                                    toast.onmouseenter = Swal.stopTimer;
                                    toast.onmouseleave = Swal.resumeTimer;
                                }
                            });
                            Toast.fire({
                                icon: "error",
                                title: "Error, please try again."
                            });
                        }


                    });

                } else {
                    $(self).prop('disabled', false);
                    $('#loadding').removeClass('v-block').addClass('v-none');
                    cancelAnimationFrame(interval);
                    count = 0;

                    const Toast = Swal.mixin({
                        toast: true,
                        position: "top-end",
                        showConfirmButton: false,
                        timer: 5000,
                        timerProgressBar: true,
                        didOpen: (toast) => {
                            toast.onmouseenter = Swal.stopTimer;
                            toast.onmouseleave = Swal.resumeTimer;
                        }
                    });
                    Toast.fire({
                        icon: "error",
                        title: "Error, please try again."
                    });
                }
            });

        });

        $('#result').on('click', 'img.preview-pic', function () {

            if (!$(this).hasClass('fullscreen')) {
                $(this).addClass('fullscreen');
                $('body').css('overflow', 'hidden');
            } else {
                $(this).removeClass('fullscreen');
                $('body').css('overflow', 'auto');
            }
        });

        $('#dropdown-base-model .dropdown-item').click(function () {
            $('#dropdown-base-model-preview').text($(this).text());

            if ($(this).text() == 'SD') {
                $('[element-model="base_model"]').attr('data-model', 'stable_diffusion');
            } else if ($(this).text() == 'XL') {
                $('[element-model="base_model"]').attr('data-model', 'stable_diffusion_xl');
            }
        });

    }, []);



    return (
        <section>

            <header>
                <Navbar />
            </header>
            <main>
                <div className="container flex-w">
                    <div className="text-center">
                        <h2>Image to Image</h2>
                    </div>


                    <div className="row justify-content-center mb-4 p-2">

                        <div className="col-md-12 col-sm-12 p-2">
                            <div className="form-group">
                                <label className="font-medium text-gray">Prompt</label>
                                <textarea id="prompt" className="form-control" rows="2" placeholder="What do you want to generate?" defaultValue="" />
                            </div>
                        </div>

                        <div className="col-md-12 col-sm-12 p-2">
                            <div className="form-group">
                                <label className="font-medium text-gray">Negative Prompt</label>
                                <textarea id="negative-prompt" className="form-control" rows="2" placeholder="" defaultValue="" />
                            </div>
                        </div>

                        <div className="col-md-12 col-sm-12 p-2">

                            <div className="row p-2">

                                {/* <div className="col-md-3 col-sm-6 col-6">
                                    
                                    <label className="font-medium text-gray">Upload Image</label>

                                    <div id="init_image" className="card-model row p-2">
                                        <div className="p-2 col-md-3 col-sm-6 col-6">
                                            <div className="box-file">
                                                <i className="fas fa-image d-block"></i>
                                                <img className="pic img-w100 d-none" src="" />
                                            </div>
                                        </div>
                                    </div>
                                </div> */}

                                {/* <div className="col-md-3 col-sm-6 col-6">

                                    <label className="font-medium text-gray">Upload Image</label>
                                    
                                    <div id="init_image" className="card-model">
                                        <div className="col-12">
                                            <div className="box-file">
                                                <i className="fas fa-image d-block"></i>
                                                <img className="pic img-w100 d-none" src="" />
                                            </div>
                                        </div>
                                    </div>
                                </div> */}

                                <div className="col-md-3 col-sm-6 col-6">
                                    <label className="font-medium text-gray text-overflow">Upload Image</label>

                                    <div id="init_image" className="card-model row">
                                        <div className="p-2 col-12">

                                            <div className="box-file-init">
                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-image d-block" />
                                                <img className="pic img-w100 d-none" src="" />
                                            </div>

                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-3 col-sm-6 col-6">
                                    <label className="font-medium text-gray text-overflow">Control Image</label>

                                    <div id="control_image" className="card-model row">
                                        <div className="p-2 col-12">

                                            <div className="box-file-control">
                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-image d-block" />
                                                <img className="pic img-w100 d-none" src="" />
                                            </div>

                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>


                        {/* <div className="col-md-12 col-sm-12 p-2">

                            <label className="font-medium text-gray">Upload Image</label>

                            <div id="init_image" className="card-model row p-2">
                                <div className="p-2 col-md-3 col-sm-6 col-6">
                                    <div className="box-file">
                                        <i className="fas fa-image d-block"></i>
                                        <img className="pic img-w100 d-none" src="" />
                                    </div>
                                </div>
                            </div>

                        </div> */}

                        <div className="col-md-12 col-sm-12 p-2">

                            <div className="row p-2">
                                <div className="col-md-3 col-sm-6 col-6">

                                    <div className="row flex">

                                        <div className="col-6">
                                            <label className="font-medium text-gray text-overflow">Base Model</label>
                                        </div>
                                        <div className="col-6">
                                            <div className="row d-flex justify-content-end mr-1">
                                                <div id="dropdown-base-model" className="dropdown">
                                                    <a id="dropdown-base-model-preview" className="dropdown-toggle text-sm font-medium text-gray" type="button" data-toggle="dropdown" aria-expanded="false">
                                                        SD
                                                    </a>
                                                    <div className="dropdown-menu">
                                                        <a className="dropdown-item text-sm font-medium text-drak">SD</a>
                                                        <a className="dropdown-item text-sm font-medium text-drak">XL</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>

                                    <div id="base-model" className="card-model row">
                                        <div className="p-2 col-12">

                                            <div className="box-model" element-model="base_model" data-model="stable_diffusion">
                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-plus d-block" />
                                                <img className="pic img-w100 d-none" src="https://pics.craiyon.com/2023-05-23/e5f819f48391433382d96ecc91bfde7f.webp" />
                                            </div>
                                            <div className="box-model-name text-center">
                                                <span className="font-medium text-gray">Null</span>
                                            </div>


                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-3 col-sm-6 col-6">
                                    <label className="font-medium text-gray text-overflow">LORA Models</label>

                                    <div id="lora-model" className="card-model row">
                                        <div className="p-2 col-12">

                                            <div className="box-model" element-model="lora_1" data-model="lora">
                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-plus d-block" />
                                                <img className="pic img-w100 d-none" src="https://pics.craiyon.com/2023-05-23/e5f819f48391433382d96ecc91bfde7f.webp" />
                                            </div>
                                            <div className="box-model-name text-center">
                                                <span className="font-medium text-gray">Null</span>
                                            </div>


                                        </div>
                                    </div>
                                </div>

                                <div className="col-md-3 col-sm-6 col-6">
                                    <label className="font-medium text-gray text-overflow">Embeddings Model</label>

                                    <div id="embeddings-model" className="card-model row">
                                        <div className="p-2 col-12">

                                            <div className="box-model" element-model="embeddings_model" data-model="embeddings">
                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-plus d-block" />
                                                <img className="pic img-w100 d-none" src="https://pics.craiyon.com/2023-05-23/e5f819f48391433382d96ecc91bfde7f.webp" />
                                            </div>
                                            <div className="box-model-name text-center">
                                                <span className="font-medium text-gray">Null</span>
                                            </div>


                                        </div>
                                    </div>
                                </div>

                            </div>

                        </div>

                        <div className="col-md-12 col-sm-12 p-2">

                            <div className="row p-2">

                                <div className="col-md-3 col-sm-6 col-6">
                                    <label className="font-medium text-gray text-overflow">ControlNet Type</label>


                                    <div id="controlnet-type-model" className="card-model row">

                                        <div className="p-2 col-12">
                                            <div className="box-model" element-model="controlnet_type_1" data-model="controlnet_type">

                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-plus d-block" />
                                                <img className="pic img-w100 d-none" src="https://pics.craiyon.com/2023-05-23/e5f819f48391433382d96ecc91bfde7f.webp" />
                                            </div>
                                            <div className="box-model-name text-center">
                                                <span className="font-medium text-gray">Null</span>
                                            </div>
                                        </div>

                                    </div>
                                </div>

                                <div className="col-md-3 col-sm-6 col-6">
                                    <label className="font-medium text-gray text-overflow">ControlNet Models</label>


                                    <div id="controlnet-model" className="card-model row">

                                        <div className="p-2 col-12">
                                            <div className="box-model" element-model="controlnet_1" data-model="controlnet">

                                                <img className="svg-close d-none" src="./svg-close.svg" />
                                                <i className="fas fa-plus d-block" />
                                                <img className="pic img-w100 d-none" src="https://pics.craiyon.com/2023-05-23/e5f819f48391433382d96ecc91bfde7f.webp" />
                                            </div>
                                            <div className="box-model-name text-center">
                                                <span className="font-medium text-gray">Null</span>
                                            </div>
                                        </div>

                                    </div>
                                </div>

                            </div>

                        </div>


                        <div className="col-md-12 col-sm-12 p-2">

                            <div className="accordion" id="accordionExample">
                                <div className="card">
                                    <div className="card-header p-0" id="headingOne">


                                        <div className="p-3 text-white" type="button" data-toggle="collapse" data-target="#collapse-option" aria-expanded="true" aria-controls="collapse-option">
                                            <div className="d-flex">
                                                <div className="font-medium text-gray" style={{ width: '97%' }}>Option</div>
                                                <div style={{ width: '3%' }} className="d-flex justify-content-center align-items-center">
                                                    <i className="fas fa-chevron-down float-right"></i>
                                                </div>
                                            </div>
                                        </div>


                                    </div>

                                    <div id="collapse-option" className="collapse row justify-content-center" aria-labelledby="headingOne" data-parent="#accordionExample">

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Render model">Scheduler</label>
                                                <select id="select-Scheduler" className="form-control">
                                                    <option value="DDPMScheduler" defaultValue>DDPMScheduler</option>
                                                    <option value="DDIMScheduler">DDIMScheduler</option>
                                                    <option value="PNDMScheduler">PNDMScheduler</option>
                                                    <option value="LMSDiscreteScheduler">LMS</option>
                                                    <option value="EulerDiscreteScheduler">Euler</option>
                                                    <option value="EulerAncestralDiscreteScheduler">Euler a</option>
                                                    <option value="DPMSolverMultistepScheduler">DPM++ 2M</option>
                                                    <option value="HeunDiscreteScheduler">Heun</option>
                                                    <option value="KDPM2DiscreteScheduler">DPM2</option>
                                                    <option value="DPMSolverSinglestepScheduler">DPMSolverSinglestepScheduler</option>
                                                    <option value="KDPM2AncestralDiscreteScheduler">DPM2 a</option>
                                                    <option value="UniPCMultistepScheduler">UniPCMultistepScheduler</option>
                                                    <option value="DDIMInverseScheduler">DDIMInverseScheduler</option>
                                                    <option value="DEISMultistepScheduler">DEISMultistepScheduler</option>
                                                    <option value="IPNDMScheduler">IPNDMScheduler</option>
                                                    <option value="KarrasVeScheduler">KarrasVeScheduler</option>
                                                    <option value="ScoreSdeVeScheduler">ScoreSdeVeScheduler</option>
                                                    <option value="LCMScheduler">LCMScheduler</option>
                                                    <option value=""></option>
                                                </select>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            {/* <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Number of images to be returned in response. The maximum value is 4.">Samples</label>
                                                <RangeSlider id="samples" min={1} max={4} step={1} tooltip="off" value={samples} onChange={changeEvent => setSamples(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>1</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{samples}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>4</div>
                                                </div>
                                            </div> */}
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Max Height: Width: 1024x1024">Image Width</label>
                                                <RangeSlider id="image-width" min={512} max={1024} step={1} tooltip="off" value={imageWidth} onChange={changeEvent => setImageWidth(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>512</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{imageWidth}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>1024</div>
                                                </div>
                                            </div>

                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Max Height: Width: 1024x1024">Image Height</label>
                                                <RangeSlider id="image-height" min={512} max={1024} step={1} tooltip="off" value={imageHeight} onChange={changeEvent => setImageHeight(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>512</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{imageHeight}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>1024</div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Scale for classifier-free guidance (minimum: 1; maximum: 20)">Guidiance Scale</label>
                                                <RangeSlider id="guidiance-scale" min={1.0} max={40.0} step={0.1} tooltip="off" value={guidianceScale} onChange={changeEvent => setGuidianceScale(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>1.0</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{guidianceScale}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>40.0</div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Number of denoising steps. The value accepts 21,31,41 and 51">Steps</label>
                                                <RangeSlider id="steps" min={11} max={51} step={1} tooltip="off" value={steps} onChange={changeEvent => setSteps(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>11</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{steps}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>51</div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Set this parameter to '2' if you want to upscale the given image resolution two times (2x), options:: 1, 2, 3">Upscale</label>
                                                <RangeSlider id="upscale" min={1} max={2} step={1} tooltip="off" value={upscale} onChange={changeEvent => setUpscale(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>1x</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{upscale + 'x'}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>2x</div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="Seed is used to reproduce results, same seed will give you same image in return again. Pass null for a random number.">Seed</label>
                                                <RangeSlider id="seed" min={1} max={9999999999999} step={1} tooltip="off" value={seed} onChange={changeEvent => setSeed(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>1</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{seed}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>999999999999</div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="form-group">
                                                <label className="font-medium text-gray text-sm" title="'guidance_scale' for controlnet, Scale for controlnet guidance. Accepts floating values from 0.1 to 5 (e.g. 0.5)">Controlnet conditioning scale</label>
                                                <RangeSlider id="controlnet_conditioning_scale" min={0.1} max={5} step={0.1} tooltip="off" value={controlnet_conditioning_scale} onChange={changeEvent => setControlnet_conditioning_scale(changeEvent.target.value)} />
                                                <div className="d-flex">
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'left' }}>0.1</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'center' }}>{controlnet_conditioning_scale}</div>
                                                    <div className="d-flex font-medium text-gray" style={{ width: '33.33%', fontSize: '12px', justifyContent: 'right' }}>5</div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3"></div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="enhance_prompt" />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="Enhance prompts for better results">
                                                        Enhance prompt
                                                    </label>
                                                </div>

                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="self_attention" />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="If you want a high quality image, set this parameter to 'yes'. In this case the image generation will take more time.">
                                                        High quality image
                                                    </label>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="use_karras_sigmas" defaultChecked />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="Use keras sigmas to generate images. gives nice results">
                                                        Use keras sigmas
                                                    </label>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="highres_fix" />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="highres fix for generated image">
                                                        Highres fix
                                                    </label>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="tomesd" defaultChecked />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="Enable tomesd to generate images: gives really fast results">
                                                        Tomesd
                                                    </label>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="auto_hint" defaultChecked />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="Auto hint image">
                                                        Auto hint
                                                    </label>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3">
                                            <div className="row pl-3 pr-3">
                                                <div className="form-check col-12">
                                                    <input className="form-check-input" type="checkbox" id="guess_mode" defaultChecked />
                                                    <label className="form-check-label font-medium text-gray text-sm" title="Set this to yes if you don't pass any prompt. The model will try to guess what's in the image and create best variations on its own. Options: yes/no">
                                                        Guess mode
                                                    </label>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="col-md-5 ml-4 mr-4 mb-3"></div>

                                    </div>
                                </div>
                            </div>



                        </div>

                        <div className="col-md-12 col-sm-12 p-2 mt-4">
                            <button id="btn-generate" type="button" className="btn btn-style text-white p-3">Generate</button>
                        </div>

                        <div className="col-md-12 col-sm-12 mt-4">

                            <div id="loadding" className="v-none">
                                <div className="col-12 d-flex justify-content-center">
                                    <div className="spinner-border" role="status" style={{ width: '1.5rem', height: '1.5rem', border: '.15em solid currentColor', borderRightColor: 'transparent', color: '#fff' }}>
                                        <span className="sr-only">Loading...</span>
                                    </div>
                                </div>
                                <div className="col-12 d-flex justify-content-center mt-2 mb-2">
                                    <span id="loadding-count" className="text-sm font-medium text-gray">0.1</span>
                                </div>
                            </div>

                            <div id="result" className="row p-2">

                            </div>
                        </div>

                        <div className="container flex-w">
                            <div className="row flex">
                                <div className="col-6">
                                    <div className="d-flex" style={{ alignItems: 'center' }}>
                                        <h3 className="">Other style</h3>
                                    </div>

                                </div>
                            </div>

                            <div className="view-models row p-2">

                                {styles}

                            </div>

                        </div>


                    </div>
                </div>

            </main>


            <div className="modal fade" id="modal-models" data-backdrop="static" data-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
                <div className="modal-dialog modal-dialog-centered flex-w">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="staticBackdropLabel">All Models</h5>
                            <a className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true"></span>
                            </a>
                        </div>
                        <div className="modal-body">
                            <div className="view-models row p-2 mb-4">

                                {viewModels}

                            </div>

                            <div className="d-flex justify-content-center">
                                <nav aria-label="Page navigation">
                                    <ul className="pagination">
                                        <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                                            <a
                                                className="page-link"
                                                onClick={() => handlePageChange(currentPage - 1)}
                                            >
                                                <i className="fas fa-chevron-left" /> {/* Font Awesome  Previous */}
                                            </a>
                                        </li>
                                        {renderPagination()}
                                        <li
                                            className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}
                                        >
                                            <a
                                                className="page-link"
                                                onClick={() => handlePageChange(currentPage + 1)}
                                            >
                                                <i className="fas fa-chevron-right" /> {/* Font Awesome  Next */}
                                            </a>
                                        </li>
                                    </ul>
                                </nav>
                            </div>

                        </div>
                        <div className="modal-footer">
                            <div className="col-md-2 col-sm-12 p-2">
                                <button id="btn-model" type="button" className="btn btn-style text-white" data-dismiss="modal" aria-label="Close">Submit</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <Score />

            <Footer />
        </section >
    )
}

export default ImageToImage;