import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

import $ from 'jquery';

import './css/Home.css';

import Navbar from './Navbar';
import Footer from './Footer';
import Score from './Score';
import { model_list } from './models/list';

export default function Model() {

    let { category } = useParams();

    const [model, setModel] = useState(1);

    const [currentPage, setCurrentPage] = useState(1);
    const [totalPages, setTotalPages] = useState(1);

    var categoryText = '';

    if (category === 'stable_diffusion') {
        categoryText = 'Stable Diffusion';
    } else if (category === 'stable_diffusion_xl') {
        categoryText = 'Stable Diffusion XL';
    } else if (category === 'lora') {
        categoryText = 'LORA';
    } else if (category === 'controlnet') {
        categoryText = 'ControlNet';
    } else if (category === 'embeddings') {
        categoryText = 'Embeddings';
    }

    const handlePageChange = (page) => {
        setCurrentPage(page);
        view_models(page);
    };

    const renderPagination = () => {
        const pagesToShow = [];
        const pagesLimit = totalPages < 7 ? totalPages : 7;
        let startPage = 1;
        if (currentPage > pagesLimit / 2) {
            startPage = currentPage - Math.floor(pagesLimit / 2);
        }

        let endPage = startPage + pagesLimit - 1;
        if (endPage > totalPages) {
            endPage = totalPages;
            startPage = endPage - pagesLimit + 1;
        }

        for (let i = startPage; i <= endPage; i++) {
            pagesToShow.push(
                <li
                    key={i}
                    className={`page-item ${currentPage === i ? 'active' : ''}`}
                    onClick={() => handlePageChange(i)}
                >
                    <a className="page-link">
                        {i}
                    </a>
                </li>
            );
        }

        return pagesToShow;
    };

    function createBlock() {
        let el = [];
        for (let i = 0; i < 12; i++) {
            el.push(
                <div key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">

                    <div style={{ width: '100%', height: '175px', backgroundColor: 'rgba(44, 51, 73, 0.2)', borderTopLeftRadius: '8px', borderTopRightRadius: '8px' }}>

                    </div>
                    <div className="text-overflow">
                        <div style={{ width: '100%', height: '24px', backgroundColor: 'rgba(44, 51, 73, 0.1)', borderBottomLeftRadius: '8px', borderBottomRightRadius: '8px' }}></div>
                    </div>

                </div>
            )
        }
        return el;
    }

    const view_models = (page) => {

        setModel(createBlock());

        model_list(category, page, 12, function (model, pages) {

            let numberPage = (pages / 12);

            if (Number.isInteger(numberPage)) {
                setTotalPages(numberPage);
            } else {
                setTotalPages(parseInt(numberPage) + 1);
            }

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'../image-to-image/' + category + '/' + (((page - 1) + ((12 - 1) * (page - 1))) + Number(i))}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }
            setTimeout(() => {
                setModel(el);
            }, 1000);

        });
    };

    useEffect(() => {

        setModel(createBlock());

        model_list(category, 1, 12, function (model, pages) {

            let numberPage = (pages / 12);

            if (Number.isInteger(numberPage)) {
                setTotalPages(numberPage);
            } else {
                setTotalPages(parseInt(numberPage) + 1);
            }

            $('#model-count').text('' + pages + ' Model');

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'../image-to-image/' + category + '/' + Number(i)}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }

            setTimeout(() => {
                setModel(el);
            }, 1000);

        });
    }, []);

    return (
        <section>

            <header>
                <Navbar />
            </header>

            <main>
                <div className="container flex-w pb-5">
                    <div className="row flex">
                        <div className="col-6">
                            <div className="d-flex" style={{ alignItems: 'center' }}>
                                <h3 className="">
                                    {categoryText}
                                </h3>
                                <span id="model-count" className="text-sm text-gray ml-2 mr-2" style={{ opacity: '0.5' }}></span>
                            </div>

                        </div>
                        <div className="col-6 "></div>
                    </div>

                    <div className="view-models row p-2 mb-4">

                        {model}

                    </div>

                    <div className="d-flex justify-content-center">
                        <nav aria-label="Page navigation">
                            <ul className="pagination">
                                <li className={`page-item ${currentPage === 1 ? 'disabled' : ''}`}>
                                    <a
                                        className="page-link"
                                        onClick={() => handlePageChange(currentPage - 1)}
                                    >
                                        <i className="fas fa-chevron-left" /> {/* Font Awesome  Previous */}
                                    </a>
                                </li>
                                {renderPagination()}
                                <li
                                    className={`page-item ${currentPage === totalPages ? 'disabled' : ''}`}
                                >
                                    <a
                                        className="page-link"
                                        onClick={() => handlePageChange(currentPage + 1)}
                                    >
                                        <i className="fas fa-chevron-right" /> {/* Font Awesome  Next */}
                                    </a>
                                </li>
                            </ul>
                        </nav>
                    </div>

                </div>

            </main>

            <Score/>

            <Footer />

        </section>

    );
}