import React, { useState, useEffect } from 'react';
import $ from 'jquery';

import './css/Home.css';

import Navbar from './Navbar';
import Footer from './Footer';
import Score from './Score';

import { Modal } from 'bootstrap';

import { model_list } from './models/list';

export default function Home(props) {

    const [stable_diffusion, setStable_diffusion] = useState(1);
    const [stable_diffusion_xl, setStable_diffusion_xl] = useState(1);
    const [lora_model, setLora_model] = useState(1);
    const [controlNet, setControlNet] = useState(1);
    const [embeddings, setEmbeddings] = useState(1);

    useEffect(() => {

        function createBlock() {
            let el = [];
            for (let i = 0; i < 8; i++) {
                el.push(
                    <div key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">

                        <div style={{ width: '100%', height: '175px', backgroundColor: 'rgba(44, 51, 73, 0.2)', borderTopLeftRadius: '8px', borderTopRightRadius: '8px' }}>

                        </div>
                        <div className="text-overflow">
                            <div style={{ width: '100%', height: '24px', backgroundColor: 'rgba(44, 51, 73, 0.1)', borderBottomLeftRadius: '8px', borderBottomRightRadius: '8px' }}></div>
                        </div>

                    </div>
                )
            }
            return el;
        }

        setStable_diffusion(createBlock());

        model_list('stable_diffusion', 1, 8, function (model, pages) {

            $('#stable_diffusion-count').text('' + pages + ' Model');

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'image-to-image/stable_diffusion/' + Number(i)}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }
            setTimeout(() => {
                setStable_diffusion(el);
            }, 1000);
        });

        setStable_diffusion_xl(createBlock());

        model_list('stable_diffusion_xl', 1, 8, function (model, pages) {

            $('#stable_diffusion_xl-count').text('' + pages + ' Model');

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'image-to-image/stable_diffusion_xl/' + Number(i)}>
                            <div className="box-pic">
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }
            setTimeout(() => {
                setStable_diffusion_xl(el);
            }, 1000);
        });

        setLora_model(createBlock());

        model_list('lora', 1, 8, function (model, pages) {

            $('#lora-count').text('' + pages + ' Model');

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'image-to-image/lora/' + Number(i)}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }
            setTimeout(() => {
                setLora_model(el);
            }, 1000);
        });

        setControlNet(createBlock());

        model_list('controlnet', 1, 8, function (model, pages) {

            $('#controlnet-count').text('' + pages + ' Model');

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'./image-to-image/controlnet/' + Number(i)}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }
            setControlNet(el);
        });

        setEmbeddings(createBlock());

        model_list('embeddings', 1, 8, function (model, pages) {

            $('#embeddings-count').text('' + pages + ' Model');

            let el = [];
            for (let i in model) {
                el.push(
                    <div id={model[i].model_id} key={i} className="models-list p-2 col-md-3 col-sm-6 col-6 text-center text-gray">
                        <a href={'./image-to-image/embeddings/' + Number(i)}>
                            <div>
                                <img className="img-w100" src={model[i].screenshots} />
                            </div>
                            <div className="text-overflow">
                                <span className="p-2 font-medium">{model[i].model_name}</span>
                            </div>
                        </a>
                    </div>
                )
            }

            setTimeout(() => {
                setEmbeddings(el);
            }, 1000);
        });

    }, []);

    return (
        <section>

            <header>
                <Navbar />
            </header>

            <main>

                <div className="container flex-w pb-5">
                    <div className="row flex">
                        <div className="col-6">
                            <div className="d-flex" style={{ alignItems: 'center' }}>
                                <h3 className="">Stable diffusion</h3>
                                <span id="stable_diffusion-count" className="text-sm text-gray ml-2 mr-2" style={{ opacity: '0.5' }}></span>
                            </div>

                        </div>
                        <div className="col-6 d-flex justify-content-end">
                            <div className="row d-flex justify-content-end mr-1" style={{ alignItems: 'center' }}>
                                <a href="model/stable_diffusion" className="nav-link" style={{ opacity: '0.5' }}>
                                    <span className="text-sm text-gray">View all</span>
                                </a>
                            </div>
                        </div>
                    </div>

                    <div className="view-models row p-2">

                        {stable_diffusion}

                    </div>

                </div>

                <div className="container flex-w pb-5">

                    <div className="row flex">
                        <div className="col-6">
                            <div className="d-flex" style={{ alignItems: 'center' }}>
                                <h3 className="">Stable diffusion XL</h3>
                                <span id="stable_diffusion_xl-count" className="text-sm text-gray ml-2 mr-2" style={{ opacity: '0.5' }}></span>
                            </div>
                        </div>
                        <div className="col-6 d-flex justify-content-end">
                            <div className="row d-flex justify-content-end mr-1" style={{ alignItems: 'center' }}>
                                <a href="model/stable_diffusion_xl" className="nav-link" style={{ opacity: '0.5' }}>
                                    <span className="text-sm text-gray">View all</span>
                                </a>
                            </div>
                        </div>
                    </div>

                    <div className="view-models row p-2">

                        {stable_diffusion_xl}

                    </div>

                </div>

                <div className="container flex-w pb-5">

                    <div className="row flex">
                        <div className="col-6">
                            <div className="d-flex" style={{ alignItems: 'center' }}>
                                <h3 className="">LORA</h3>
                                <span id="lora-count" className="text-sm text-gray ml-2 mr-2" style={{ opacity: '0.5' }}></span>
                            </div>
                        </div>
                        <div className="col-6 d-flex justify-content-end">
                            <div className="row d-flex justify-content-end mr-1" style={{ alignItems: 'center' }}>
                                <a href="model/lora" className="nav-link" style={{ opacity: '0.5' }}>
                                    <span className="text-sm text-gray">View all</span>
                                </a>
                            </div>
                        </div>
                    </div>

                    <div className="view-models row p-2">

                        {lora_model}

                    </div>

                </div>

                <div className="container flex-w pb-5">

                    <div className="row flex">
                        <div className="col-6">
                            <div className="d-flex" style={{ alignItems: 'center' }}>
                                <h3 className="">ControlNet</h3>
                                <span id="controlnet-count" className="text-sm text-gray ml-2 mr-2" style={{ opacity: '0.5' }}></span>
                            </div>
                        </div>
                        <div className="col-6 d-flex justify-content-end">
                            <div className="row d-flex justify-content-end mr-1" style={{ alignItems: 'center' }}>
                                <a href="model/controlnet" className="nav-link" style={{ opacity: '0.5' }}>
                                    <span className="text-sm text-gray">View all</span>
                                </a>
                            </div>
                        </div>
                    </div>

                    <div className="view-models row p-2">

                        {controlNet}

                    </div>

                </div>

                <div className="container flex-w pb-5">

                    <div className="row flex">
                        <div className="col-6">
                            <div className="d-flex" style={{ alignItems: 'center' }}>
                                <h3 className="">Embeddings</h3>
                                <span id="embeddings-count" className="text-sm text-gray ml-2 mr-2" style={{ opacity: '0.5' }}></span>
                            </div>
                        </div>
                        <div className="col-6 d-flex justify-content-end">
                            <div className="row d-flex justify-content-end mr-1" style={{ alignItems: 'center' }}>
                                <a href="model/embeddings" className="nav-link text-sm text-gray" style={{ opacity: '0.5' }}>View all</a>
                            </div>
                        </div>
                    </div>

                    <div className="view-models row p-2">
                        {embeddings}
                    </div>
                </div>

            </main>

            <Score/>
            
            <Footer />

        </section>

    );
}