import { useState } from 'react';

import './css/Navbar.css';

import 'bootstrap';

export default function Footer() {

    const [footer, setFooter] = useState(1);

    let el = [];
    el.push(
        <footer key={0} className="footer p-3">
            <div className="container">
                <div className="copyright">
                    <div className="row">
                        <div className="col-md-6 text-white text-center">
                             2023 Artpix-AI Officials. All rights reserved
                        </div>
                        <div className="col-md-6 text-center">
                            <div className="footer-menu">
                                <a href="https://www.iubenda.com/privacy-policy/63833862" className="iubenda-white iubenda-noiframe iubenda-embed iubenda-noiframe " title="Privacy Policy " style={{ outline: 0, border: 0, textDecoration: 'none', display: 'inline-block', background: 'none', width: 116, height: 25 }}>Privacy Policy</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </footer>
    );

    setTimeout(() => {
        setFooter(el);
    });

    return (
        <>
            {footer}
        </>
    );
}