    "description": "<p>This model is an&nbsp;<a href=\"https://discord.gg/unstablediffusion\" target=\"_blank\" rel=\"noopener ugc\">Unstable Diffusion</a>&nbsp;community project and wouldn't have been possible to train without their generous help and resources. A heartfelt thank you to them and everyone who's contributed their data, time, and experience!</p>",
