    "description": "<p><strong>MeinaPastel&nbsp;</strong>aims to make illustrations with a&nbsp;<strong>2d feeling</strong>&nbsp;to them with good light, shadows and details, making&nbsp;<strong>pastel</strong>&nbsp;<strong>or colorful</strong>&nbsp;images!<br><br>I created a&nbsp;<a href=\"https://discord.gg/F6yeQtEQ98\" target=\"_blank\" rel=\"noopener ugc\">discord</a>&nbsp;server where you can post images that you generated, discuss prompt and/or ask for help.&nbsp;<a href=\"https://discord.gg/meinaverse\" target=\"_blank\" rel=\"noopener ugc\">https://discord.gg/meinaverse</a><br><br><strong>If you like one of my models and want to support their updates:</strong><br>I have a&nbsp;<a href=\"https://ko-fi.com/meina\" target=\"_blank\" rel=\"noopener ugc\"><strong>ko-fi</strong></a>&nbsp;and&nbsp;<a href=\"https://www.patreon.com/MeinaMix\" target=\"_blank\" rel=\"noopener ugc\"><strong>Patreon</strong></a>&nbsp;page where you can support me or buy me a coffee &lt;3 ,&nbsp;<strong>it will be very much appreciated:</strong>&nbsp;<a href=\"https://ko-fi.com/meina\" target=\"_blank\" rel=\"noopener ugc\"><strong>https://ko-fi.com/meina</strong></a><strong>&nbsp;and&nbsp;</strong><a href=\"https://www.patreon.com/MeinaMix\" target=\"_blank\" rel=\"noopener ugc\"><strong>https://www.patreon.com/MeinaMix</strong></a><br><br><strong>You may also try my model using</strong>&nbsp;<a href=\"https://sinkin.ai/m/xylZzvg\" target=\"_blank\" rel=\"noopener ugc\">Sinkin.ai</a>:&nbsp;<a href=\"https://sinkin.ai/m/xylZzvg\" target=\"_blank\" rel=\"noopener ugc\">https://sinkin.ai/m/xylZzvg</a><br><br>---------------------------------------------<br><strong>Recommendations:</strong><br><strong>Sampler:</strong>&nbsp;DPM++ 2M Karras: 20 steps.<br><strong>Resolutions:</strong>&nbsp;512x768, 512x1024. ( other resolutions may work).<br><strong>Hires.fix option 1:&nbsp;</strong>Latent, with 10 steps and 0.55 denoising<strong>.</strong><br><strong>Hires.fix option 2:</strong>&nbsp;R-ESRGAN 4x or +Anime6b, with 15 steps at 0.1 up to 0.3 denoising.<br><strong>Clip Skip:</strong>&nbsp;2.<br><strong>Negatives:</strong>&nbsp;' (worst quality, low quality:1.4), monochrome, zombie '<br><br><strong>Prompt:</strong>&nbsp;' colorful ' and ' high contrast ' makes the model look more 2d and with colors more vivid.<br>---------------------------------------------</p>\n<p><br>V4 is a merge between v3 with an amazing model created by Yozora:&nbsp;<a href=\"https://civitai.com/models/21200/color-box-model\" target=\"_blank\" rel=\"noopener ugc\">color-box-model</a>&nbsp;and animelike2d</p>",
