    "description": "<h3>Tired of SD producing overblown, bright images?</h3>\n<p>&nbsp;</p>\n<p>This LORA model is trained using the technique described in&nbsp;<a href=\"https://www.crosslabs.org//blog/diffusion-with-offset-noise\" target=\"_blank\" rel=\"noopener ugc\">https://www.crosslabs.org//blog/diffusion-with-offset-noise</a></p>\n<p>&nbsp;</p>\n<p>Use it to produce beautiful, high-contrast, low-key images that SD just wasn't capable of creating until now.</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>Sample images were generated using&nbsp;<a href=\"https://huggingface.co/IlluminatiAI/Illuminati_Diffusion_v1.0\" target=\"_blank\" rel=\"noopener ugc\">Illuminati Diffusion v1.0</a></p>",
