    "featured": "no",
    "description": "<p>Hey everyone!<br>I'm excited to showcase this fantastic SD model, created by the incredibly talented <strong><a href=\"https://civitai.com/user/Aitasai/models\">Aitasai</a></strong>. All credit for this model goes to them. </p>\n<p>It's essential to support original creators, and if you're as impressed with their work as I am, check out their official page and show them some love: <strong><a href=\"https://civitai.com/user/Aitasai/models\">Aitasai</a></strong>.</p>\n<p>Transparency and respect for artists are vital - &nbsp;If <strong><a href=\"https://civitai.com/user/Aitasai/models\">Aitasai</a></strong> or anyone related feels uncomfortable with this model being showcased here, please reach out, and we'll take the necessary steps.</p>\n<p>---------------------------------------------</p>\n<p><strong>Original Model Description</strong></p>\n<p>Version (V2.25D)</p>\n<p>&nbsp;</p>\n<p>Recommend:</p>\n<p>vae-ft-mse-840000-ema</p>\n<p>&nbsp;</p>\n<p>use highres fix to improve quality.</p>\n<p>&nbsp;</p>\n<p></p>\n<p>2.5d</p>\n<p>2.5D2.25d</p>\n<p><s>noiseoffset</s></p>\n<p>&nbsp;</p>\n<p>After playing Tears of the Kingdom for a month, I resumed my old work. The new version is an integration of 2.5d, which retains the overall anime style while being better than the previous versions on the limbs, but the light and shadow and lines are more like 2.5D, so i simply call it 2.25d version.</p>\n<p></p>\n<p>darkdark </p>\n<p></p>\n<p></p>\n<p><a href=\"https://civitai.com/models/21409/five-nuts-mixed-mix\" target=\"_blank\" rel=\"noopener ugc\">Five Nuts Mixed Mix | Stable Diffusion Checkpoint | Civitai </a><a href=\"https://civitai.com/models/23905/unrule\" target=\"_blank\" rel=\"noopener ugc\">Unrule | Stable Diffusion Checkpoint | Civitai</a></p>\n<p>TmndMix  <a href=\"https://civitai.com/models/27259\" target=\"_blank\" rel=\"noopener ugc\">https://civitai.com/models/27259</a></p>\n<p>darkernoiseoffsetlorabrighterbrighternoiseoffsetlora</p>\n<p>&nbsp;</p>\n<p>Intro:</p>\n<p>named from a dark soul's meme in Chinese community.</p>\n<p>&nbsp;</p>\n<p>merged with my friend's pravite model and some other models merged by myself.</p>\n<p>Including <a href=\"https://civitai.com/models/21409/five-nuts-mixed-mix\" target=\"_blank\" rel=\"noopener ugc\">Five Nuts Mixed Mix | Stable Diffusion Checkpoint | Civitai </a>,</p>\n<p><a href=\"https://civitai.com/models/23905/unrule\" target=\"_blank\" rel=\"noopener ugc\">Unrule | Stable Diffusion Checkpoint | Civitai</a></p>\n<p>and my friend's TmndMix <a href=\"https://civitai.com/models/27259\" target=\"_blank\" rel=\"noopener ugc\">https://civitai.com/models/27259</a> .it's definitely a masterpiece.</p>\n<p>if you like dark sushi ,you should also try tmnd.</p>\n<p>noiseoffset lora is merged into the darker version. So the imgs generated by the darker version will be darker ,and have better light and shadow performance. you can use brighter version and noiseoffset lora to get similar effect.</p>",
