    "featured": "no",
    "description": "<p>Please note that the safe range for good gens is between 768-1024 / 1152 for some seeds and prompts / 1280x768 or 768x1280 in rare instances will also work. This seems to be relatively fundamental to SD2.1.<br><br>Please see example images to get an idea how to prompt. No specific trigger words are necessary, this is a full finetune.<br><br>If you are an advanced user, as always, go ahead and just experiment with negative prompting or other embeds, but please not that this model was trained with offset noise, as such, other embeddings may reduce the quality of the contrast that has been introduced.<br><br>Please note that some of the sample gens have SD upscale applied to them, to reproduce, please omit this info when importing into AUTO1111 webui.</p>",
