    "description": "<p><strong>MeinaAlter doesn't have an objective. It serves as a way for me to upload models that doesn't fit in the other 4 Meinas.</strong><br><br>I created a&nbsp;<a href=\"https://discord.gg/F6yeQtEQ98\" target=\"_blank\" rel=\"noopener ugc\">discord</a>&nbsp;server where you can post images that you generated, discuss prompt and/or ask for help.&nbsp;<a href=\"https://discord.gg/F6yeQtEQ98\" target=\"_blank\" rel=\"noopener ugc\">https://discord.gg/F6yeQtEQ98</a>&nbsp;( i'll be helping and answering questions there whenever i can! )<br><br><strong>If you like one of my models and want to support their updates:</strong><br>I have a&nbsp;<a href=\"https://ko-fi.com/meina\" target=\"_blank\" rel=\"noopener ugc\"><strong>ko-fi</strong></a>&nbsp;and&nbsp;<a href=\"https://www.patreon.com/MeinaMix\" target=\"_blank\" rel=\"noopener ugc\"><strong>Patreon</strong></a>&nbsp;page where you can support me or buy me a coffee &lt;3 ,&nbsp;<strong>it will be very much appreciated:</strong>&nbsp;<a href=\"https://ko-fi.com/meina\" target=\"_blank\" rel=\"noopener ugc\"><strong>https://ko-fi.com/meina</strong></a><strong>&nbsp;and https://www.patreon.com/MeinaMix</strong><br><br><strong>MeinaAlter and the other of Meinas will ALWAYS be FREE.</strong></p>\n<p>The images in the samples aren't cherry picked, if i had spent 2h to cherry pick each of the images it would hide how the model really performs and i don't think thats fair.<br><br><strong>The recommendations are just basic guidelines, you're welcome to use your own parameters or change them to your liking!!</strong></p>",
