    "description": "<p><strong>dvArch</strong>&nbsp;is a custom trained model that uses three separate trigger words:</p>\n<p>dvArchModern</p>\n<p>dvArchGothic</p>\n<p>dvArchVictorian</p>\n<p>&nbsp;</p>\n<p>Each of the trigger words will create buildings in that style of architecture. You can also use the three trigger words together to generate whole cities in a mix of buildings in those styles.</p>\n<p>&nbsp;</p>\n<p><strong>dvArchInterior</strong>&nbsp;is a custom trained model that uses three separate trigger words:</p>\n<p>dvArchInteriorModern</p>\n<p>dvArchInteriorGothic</p>\n<p>dvArchInteriorVictorian<br><br>Each of the trigger words will create interior rooms in that style of building. Use \"<em>bedroom</em>\" \"<em>living room</em>\" \"<em>bathroom</em>\" etc to specify type of room.</p>",
