    "description": "<h2>SamDoesArts (Sam Yang) style LoRA</h2>\n<p>&nbsp;</p>\n<p>There are already countless models for this artstyle, but I still wanted to test out how LoRA's can handle it. I also wanted to experiment with&nbsp;<a href=\"https://huggingface.co/m4gnett/any-pastel\" target=\"_blank\" rel=\"noopener ugc\"><strong>AnyPastel</strong></a>&nbsp;(which is a mix between AnyLoRA and PastelMix). This LoRA didn't come out super strong, so it has the right flexibility to get a style that's closer to Sam Yang's on AnyPastel, while still reminding of him on other models. Check the examples (mainly the model hash) to get what I mean. Triggers with&nbsp;<code>sam yang</code>&nbsp;and I suggest AnyPastel as base model (or, if you don't care about having a more matching artstyle, AnyLoRA or AbyssOrangeMix2).&nbsp;<strong>Use weight=1 for the offset version or 0.65 for the original (check \"original\" tab for a comparison)</strong>.</p>",
