    "is_nsfw": "no",
    "featured": "no",
    "description": "<p>Hey everyone!<br>I'm excited to showcase this fantastic SD model, created by the incredibly talented <a href=\"https://civitai.com/user/s6yx/models\"><strong>s6yx</strong></a>. All credit for this model goes to them. </p>\n<p>It's essential to support original creators, and if you're as impressed with their work as I am, check out their official page and show them some love:&nbsp;<strong><a href=\"https://civitai.com/user/s6yx/models\">s6yx</a></strong>.</p>\n<p>Transparency and respect for artists are vital - &nbsp;If <a href=\"https://civitai.com/user/s6yx/models\"><strong>s6yx</strong></a> or anyone related feels uncomfortable with this model being showcased here, please reach out, and we'll take the necessary steps.</p>\n<p>---------------------------------------------</p>\n<p><strong>Original Model Description</strong></p>\n<p>Version (V1.2.2)</p>\n<div class=\"mantine-150s3z1 mantine-Spoiler-content\" style=\"max-height: 2338.6px;\">\n<div>\n<div class=\"mantine-TypographyStylesProvider-root mantine-1c4hq9n\">\n<div>\n<h3>Quick Update:</h3>\n<p>I've been a bit burnt out on SD model development (SD in general tbh) and that is the reason there have not been an update. Looking to come back around and develop again by next month or so.<br><br>Thank you everyone who sends reviews and enjoy my model<br><br></p>\n<p><strong>Pay attention to the <em><u>About this version</u></em></strong> <strong>section </strong>of model page<strong> for specific version information. </strong></p>\n<h3><br><br><u>Model Overview:</u></h3>\n<ul>\n<li>\n<p><u>rev</u> or <u>revision</u>: The concept of how the model generates images is likely to change as I see fit.</p>\n</li>\n<li>\n<p><u>Animated</u>: The model has the ability to create 2.5D like image generations. This model is a checkpoint merge, meaning it is a product of other models to create a product that derives from the originals.</p>\n</li>\n<li>\n<p>Kind of generations:</p>\n<ul>\n<li>\n<p>Fantasy</p>\n</li>\n<li>\n<p>Anime</p>\n</li>\n<li>\n<p>semi-realistic</p>\n</li>\n<li>\n<p><em>decent Landscape</em></p>\n</li>\n</ul>\n</li>\n<li>\n<p>LoRA friendly</p>\n</li>\n<li>\n<p>It works <strong><em><u>best on these resolution dimensions:</u></em></strong></p>\n<ul>\n<li>\n<p>512x512</p>\n</li>\n<li>\n<p>512x768</p>\n</li>\n<li>\n<p>768x512</p>\n</li>\n</ul>\n</li>\n</ul>\n<p>&nbsp;</p>\n<h3><u>VAE</u>:</h3>\n<ul>\n<li>\n<p><a href=\"https://huggingface.co/WarriorMama777/OrangeMixs/blob/main/VAEs/orangemix.vae.pt\" target=\"_blank\" rel=\"noopener ugc\"><u>orangemix.vae.pt</u></a></p>\n</li>\n<li>\n<p><a href=\"https://huggingface.co/hakurei/waifu-diffusion-v1-4/tree/main/vae\" target=\"_blank\" rel=\"noopener ugc\">kl-f8-anime2.ckpt</a></p>\n</li>\n<li>\n<p><a href=\"https://huggingface.co/NoCrypt/blessed_vae/blob/main/blessed2.vae.pt\" target=\"_blank\" rel=\"noopener ugc\">Blessed2.vae.pt</a></p>\n<p>&nbsp;</p>\n</li>\n</ul>\n<h3><u>Prompting</u>:</h3>\n<ul>\n<li>\n<p><strong>Order matters</strong> - words near the front of your prompt are weighted more heavily than the things in the back of your prompt.</p>\n</li>\n<li>\n<p><strong>Prompt order</strong> - content type &gt; description &gt; style &gt; composition</p>\n</li>\n<li>\n<p><strong>This model likes</strong>: ((best quality)), ((masterpiece)), (detailed) in beginning of prompt if you want anime-2.5D type</p>\n</li>\n<li>\n<p>This model does great on<strong> <u>PORTRAITS</u></strong></p>\n</li>\n</ul>\n<p>&nbsp;</p>\n<p><strong><u>Negative Prompt Embeddings:</u></strong></p>\n<ul>\n<li>\n<p><a href=\"https://huggingface.co/embed/EasyNegative/tree/main\" target=\"_blank\" rel=\"noopener ugc\">EasyNegative</a></p>\n</li>\n<li>\n<p><a href=\"https://civitai.com/models/4629/deep-negative-v1x\" target=\"_blank\" rel=\"noopener ugc\">Deep Negative</a></p>\n</li>\n<li>\n<p><a href=\"https://huggingface.co/embed/bad_prompt/blob/main/bad_prompt_version2.pt\" target=\"_blank\" rel=\"noopener ugc\">bad_prompt_version2</a></p>\n</li>\n<li>\n<p><a href=\"https://huggingface.co/nick-x-hacker/bad-artist/blob/main/bad-artist.pt\" target=\"_blank\" rel=\"noopener ugc\">bad-artist</a></p>\n</li>\n<li>\n<p><a href=\"https://huggingface.co/nick-x-hacker/bad-artist/blob/main/bad-artist-anime.pt\" target=\"_blank\" rel=\"noopener ugc\">bad-artist-anime</a></p>\n</li>\n<li>\n<p><a href=\"https://huggingface.co/p1atdev/badquality/tree/main\" target=\"_blank\" rel=\"noopener ugc\">bad-quality</a></p>\n</li>\n<li>\n<p>Make use of weights in negative prompts (i.e (worst quality, low quality:1.4))</p>\n<p>&nbsp;</p>\n</li>\n</ul>\n<p>&nbsp;</p>\n<h3><u>Video Features</u></h3>\n<p>&nbsp;</p>\n<p><a href=\"https://youtu.be/Nl43zR5dVuM?t=192\" target=\"_blank\" rel=\"noopener ugc\">Olivio Sarikas - Why Is EVERYONE Using This Model?! - Rev Animated for Stable Diffusion / A1111</a></p>\n<p><a href=\"https://www.youtube.com/watch?v=A6dQPMy_tHY\" target=\"_blank\" rel=\"noopener ugc\">Olivio Sarikas - ULTRA SHARP Upscale! - Don't miss this Method!!! / A1111 - NEW Model</a><br><br><a href=\"https://www.youtube.com/watch?v=ezNDCWhv4pQ\" target=\"_blank\" rel=\"noopener ugc\">AMAZING SD Models - And how to get the MOST out of them!</a></p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<h2><strong><u>Disclaimer:</u></strong></h2>\n<p><strong>Do not sell</strong> this model on any website without permissions from creator (me)</p>\n<p><strong>Credit</strong> me if you use my model in your own merges</p>\n<p><strong>I do not authorize</strong> this model to be used on <strong>generative services</strong></p>\n<p>I have given you plentiful amount information and sources within this section, <strong>I will not answer</strong> <strong><u>redundant questions</u></strong> if it already exists here in the info section.<br><br></p>\n<p>if you would like to support me:<br><a href=\"https://ko-fi.com/s6yx0\" target=\"_blank\" rel=\"noopener ugc\">https://ko-fi.com/s6yx0</a></p>\n<h2>&nbsp;</h2>\n</div>\n</div>\n</div>\n</div>",
