    "description": "<h1><strong>Night Sky YOZORA Model &mdash;&mdash;&ldquo;For ultimate image quality and large image size&gt;1536 x 1024&rdquo;</strong></h1>\n<h3><strong>Trained by&nbsp;<a href=\"https://civitai.com/user/YozoRaAru\">@YozoRaAru</a></strong></h3>\n<h2><strong><em><u></u></em></strong><a href=\"https://civitai.com/models/21200/color-box-model\" target=\"_blank\" rel=\"noopener ugc\"><strong><em><u>Color Box</u></em></strong></a></h2>\n<h2><strong><em><u>If you want better colour representation There is a new style model&nbsp;</u></em></strong><a href=\"https://civitai.com/models/21200/color-box-model\" target=\"_blank\" rel=\"noopener ugc\"><strong><em><u>Color Box</u></em></strong></a></h2>\n<p><strong>YOZORA</strong>&nbsp;is a model that pursues perfection and is&nbsp;<strong>filled with personal preferences</strong>. I trained it using images that I like, which give it an unparalleled level of detail and completion. It can provide you with very exquisite character images.</p>\n<p>In addition to training, I also blended it with dozens of other high-quality models that I personally like, and I tried layer blending as well. Among these models, I selected NovelAI's original model as the major weight because it can give it excellent adaptability to a variety of prompts. I haven't tried testing it with landscape + character yet, but I believe it can also perform excellently in that aspect.</p>\n<ul>\n<li>\n<p>For the&nbsp;<strong><em>best results</em></strong>, please ensure that your&nbsp;<strong><em>final resolution &gt; 1536x1024</em></strong>. This is&nbsp;<strong><em>necessary</em></strong>&nbsp;for generating exquisite illustrations</p>\n<p>&nbsp;</p>\n</li>\n</ul>\n<p>Here are some recommended settings for the parameters:</p>\n<ul>\n<li>\n<p><strong>Clip skip</strong>: It is recommended to set it at no less than 2.&nbsp;<strong>YOZORA</strong>&nbsp;already has a rich level of detail, and setting it at 1 may make the image appear cluttered and confusing.</p>\n</li>\n<li>\n<p><strong>Resolution</strong>:&nbsp;<strong>YOZORA</strong>&nbsp;may&nbsp;<strong>not be suitable for generating small images</strong>&nbsp;because the extreme level of detail may become too crowded. It is recommended&nbsp;<strong>do not use Hires.fix</strong>&nbsp;to generate&nbsp;<strong>large images</strong>. A larger resolution is equivalent to a larger canvas, making it easier for YOZORA to capture details. It is recommended to use&nbsp;<strong>1536 x 1024</strong>&nbsp;or higher for Hires.fix.</p>\n</li>\n<li>\n<p><strong>Negative prompts</strong>: It is recommended to use&nbsp;<strong>EasyNegative</strong>&nbsp;to provide brief and precise descriptions.</p>\n</li>\n</ul>\n<p>The name YOZORA means&nbsp;<strong>\"strolling among the stars in the night sky\"</strong>. I hope that she can bring you the excitement and joy of a sky full of stars on a starry night.</p>",
