    "description": "<h1 class=\"mantine-Text-root mantine-Title-root mantine-1tq2xbx\">GTA5 Artwork Diffusion LORA Extraction</h1>\n<div class=\"mantine-Badge-root mantine-yu41fg\">&nbsp;</div>\n<p>&nbsp;</p>",
