    "api_calls": "240",
    "model_category": "lora",
    "model_name": "Ui-Ux",
    "is_nsfw": "no",
    "featured": "no",
    "description": "<p>Ui-Ux (LORA) - A tool to speed up your concept workflow, not to replace it.</p>\n<p>As someone with a design degree, I'm constantly trying to think of things on fly and I can't - I just can't, and clearly these won't REPLACE the process - and while a LOT OF MODELS CAN DO THIS without it - I figured adding a LORA wouldn't hurt.</p>\n<p>It was trained on some really cool design concepts, and frankly I'm pretty proud of it.</p>\n<p>Clearly our prompts are not perfect for this, and it can get a little wild - but it'll be great to start spicing up the design world a bit more!</p>\n<p>&nbsp;</p>\n<p>https://civitai.com/models/98715/ui-ux-lora</p>",
