    "description": "<p>V7 is here. So far so good for me.</p>\n<p>Recommended Parameters for V7:</p>\n<p>Sampler:&nbsp;<strong>Euler a, Euler, restart</strong></p>\n<p>Steps: 20~40</p>\n<p>Hires upscaler:&nbsp;<strong>ESRGAN 4x or 4x-UltraSharp or 8x_NMKD-Superscale_150000_G</strong></p>\n<p>Hires upscale: 2+</p>\n<p>Hires steps: 15+</p>\n<p>Hires denoising strength: 0.05~0.5</p>\n<p><strong>clip skip 2</strong></p>\n<p>&nbsp;</p>\n<p><strong>after detailer.</strong></p>\n<p><strong>If your face comes out badly, use after detailer instead of face restoration.</strong></p>\n<p><a href=\"https://github.com/Bing-su/adetailer\" target=\"_blank\" rel=\"noopener ugc\"><strong>https://github.com/Bing-su/adetailer</strong></a></p>\n<p>&nbsp;</p>\n<p><strong>Dynamic Thresholdingcfg1~20</strong></p>\n<p><strong>Use Dynmaic Thresholding to control CFG. You can try from 1~20.</strong></p>\n<p><a href=\"https://github.com/mcmonkeyprojects/sd-dynamic-thresholding\" target=\"_blank\" rel=\"noopener ugc\">https://github.com/mcmonkeyprojects/sd-dynamic-thresholding</a></p>\n<p>&nbsp;</p>\n<p><strong>BMAB.</strong></p>\n<p><strong>If you want to add noise like me, use BMAB.</strong></p>\n<p><a href=\"https://github.com/portu-sim/sd-webui-bmab\" target=\"_blank\" rel=\"noopener ugc\">GitHub - portu-sim/sd-webui-bmab: Auto masking and inpainting for person, face, hand. Resizing image using detection model.</a></p>",
