    "description": "<h2 id=\"heading-734\">Add More Details - Detail Enhancer / Tweaker</h2>\n<p><strong>Do you like what I do? Consider supporting me on&nbsp;</strong><a href=\"https://www.patreon.com/Lykon275\" target=\"_blank\" rel=\"noopener ugc\"><strong>Patreon</strong></a><strong>&nbsp; or feel free to&nbsp;</strong><a href=\"https://snipfeed.co/lykon\" target=\"_blank\" rel=\"noopener ugc\"><strong>buy me a coffee</strong></a><strong>&nbsp;</strong></p>\n<p>I've been using the (great) Detail Tweaker by CyberAIchemist for a while now, and I was really curious. I really just wanted to know if I was capable of doing something similar.</p>\n<p>I had a lot of fun doing this, and I think it came out pretty great. I wil ldefinitely incorporate it in most of my work going forward.</p>\n<p>You should use this&nbsp;<strong>between 0.5 and 1 weight</strong>, depending on your preference. You can go lower than 0.5 for a more subtle effect, of course.</p>\n<p>I also found out that this gives some interesting results at negative weight, sometimes. See the examples to see what I mean.</p>\n<p><strong>How to use LoRA's in auto1111:</strong></p>\n<ul>\n<li>\n<p>Update webui (use&nbsp;<code>git pull</code>&nbsp;<a href=\"https://www.youtube.com/embed/mn8fMF10XN4?start=31&amp;end=60\" target=\"_blank\" rel=\"noopener ugc\">like here</a>&nbsp;or redownload it)</p>\n</li>\n<li>\n<p>Copy the file to&nbsp;<code>stable-diffusion-webui/models/lora</code></p>\n</li>\n<li>\n<p>Select your LoRA like in&nbsp;<a href=\"https://www.youtube.com/watch?v=-bMeyXOZwN0\" target=\"_blank\" rel=\"noopener ugc\">this video</a></p>\n</li>\n<li>\n<p><strong>Make sure to change the weight</strong>&nbsp;(by default it's&nbsp;<code>:1</code>&nbsp;which is usually too high)</p>\n</li>\n</ul>",
