    "featured": "no",
    "description": "<h2 id=\"heading-2074\">All in One / Any Case Version</h2>\n<ol>\n<li>\n<p><strong>The shorter the prompt &ndash; the better the result</strong><br>You can now forget about&nbsp;<code>extremely detailed</code>,&nbsp;<code>8k</code>,&nbsp;<code>hyperdetailed</code>,&nbsp;<code>masterpiece</code>, etc. You can still use them, but it's not necessary, because you'll get a masterpiece anyway. Just take a look at the demo images and their EXIF data.<br><u>Masterpiece tip</u>:<br>If you still gonna use&nbsp;<code>masterpiece</code>&nbsp;token in your Prompt, trust me, the real power of this token you can see only if you keep your Negative prompt empty.</p>\n</li>\n<li>\n<p><strong>Trigger Words</strong><br>You can boost the result by adding the&nbsp;<code>mj</code>&nbsp;token (at the start of the prompt),&nbsp;<code>cozy</code>&nbsp;or&nbsp;<code>cinematic</code>. You probably guess what&nbsp;<code>mj</code>&nbsp;means and how the magic works. So you have it.</p>\n</li>\n<li>\n<p><strong>Cinematic</strong><br>I've completely revamped the&nbsp;<code>cinematic</code>&nbsp;token. As you can see, Deliberate v3 always strives to find reflections and create stunning results (without crazy prompting), much like Midjourney does.<br>At the same time, sometimes we want to achieve the highest level that no photographer can reach. That's why I've redefined the interpretation of&nbsp;<code>cinematic</code>.</p>\n</li>\n<li>\n<p><strong>NSFW</strong><br><strong>Oh yeah</strong>, I've rebuilt many of the NSFW tokens to deliver perfect results with just one word. You can find all the details on&nbsp;<a href=\"https://boosty.to/xpuct\" target=\"_blank\" rel=\"noopener ugc\">my Boosty page</a>.</p>\n</li>\n<li>\n<p><strong>Dataset</strong><br>The dataset is truly enormous. In fact, this is the first public model on the internet, where the selection of images was stricter than anywhere else, including Midjourney. Deliberate v3 can work without negatives and still produce masterpieces. This became possible precisely because of the huge dataset. It's unique, it's massive, and it includes only perfect images. Just try typing any word, exclude the negatives, and you'll see that Deliberate knows what to show you without randomness. Even when you're trying to get some ugly result, it still provides you with a spectacular one.</p>\n</li>\n<li>\n<p><strong>License</strong><br>The license type is&nbsp;<a href=\"https://creativecommons.org/licenses/by-nc-nd/4.0/\" target=\"_blank\" rel=\"noopener ugc\"><strong>CC BY-NC-ND 4.0</strong></a>. If you want to use Deliberate on your service and/or for commercial purposes, message me on&nbsp;<a href=\"https://discord.gg/xpuct\" target=\"_blank\" rel=\"noopener ugc\">Discord</a>. If you are an individual, not a commercial organization, there are no restrictions for you. The philosophy is very simple &ndash; private individuals provide support for the model, not companies, so private individuals can do anything.</p>\n</li>\n<li>\n<p><strong>Easter Egg</strong><br>Now you can stop arguing with people to prove what the best sampler in the world is. You can prompt my nickname on a 512 x 512 and see how good the image looks, count how many fingers the girl has, and check if there is correct sign text or not.<br>From now on, you can just run batch Euler a and UniPC, for example, and see who make the job better.</p>\n</li>\n</ol>",
