    "model_name": "relolahrichallenger",
    "is_nsfw": "no",
    "featured": "no",
    "description": "<p>Hey everyone!<br>I'm excited to showcase this fantastic SD model, created by the incredibly talented <strong><a href=\"https://civitai.com/user/AhriMain/models\">AhriMain</a></strong>. All credit for this model goes to them. </p>\n<p>It's essential to support original creators, and if you're as impressed with their work as I am, check out their official page and show them some love: <strong><a href=\"https://civitai.com/user/AhriMain/models\">AhriMain</a></strong>.</p>\n<p>Transparency and respect for artists are vital - If <strong><a href=\"https://civitai.com/user/AhriMain/models\">AhriMain</a></strong> or anyone related feels uncomfortable with this model being showcased here, please reach out, and we'll take the necessary steps.</p>\n<p>---------------------------------------------</p>\n<p><strong>Original Model Description</strong></p>\n<p>&nbsp;</p>\n<div class=\"mantine-150s3z1 mantine-Spoiler-content\" style=\"max-height: 1027.6px;\">\n<div>\n<div class=\"mantine-TypographyStylesProvider-root mantine-1c4hq9n\">\n<div>\n<p>Lora trained on Challenger Ahri from League of Legends</p>\n<p>&nbsp;</p>\n<p>requested by: <a href=\"https://civitai.com/user/wodao\">@wodao</a></p>\n<p>&nbsp;</p>\n<p>tags: challenger ahri, 1girl, bodysuit, helmet, jacket, animal ears, whisker markings, cleavage, long hair, white hair</p>\n<p>&nbsp;</p>\n<p>optional tags: multiple tails, energy ball</p>\n<p>You definitely need more tries on those ones since Stable Diffusion doesn't like counting</p>\n<p>&nbsp;</p>\n<p>anime: <a href=\"https://civitai.com/models/104100/kawaii-realistic-anime-mix\" target=\"_blank\" rel=\"noopener ugc\">Kawaii Realistic Anime Mix - A0.2</a>, <a href=\"https://civitai.com/models/28648/yabalmix\" target=\"_blank\" rel=\"noopener ugc\">YabaL_Mix - V5</a></p>\n<p>2,5D: <a href=\"https://civitai.com/models/107842\" target=\"_blank\" rel=\"noopener ugc\">Aniverse - Aniverse V1.0</a>, <a href=\"https://civitai.com/models/67994/pasteldiffusedmix\" target=\"_blank\" rel=\"noopener ugc\">PastelDiffusedMix - V2.2</a>, <a href=\"https://civitai.com/models/18798/meinaunreal\" target=\"_blank\" rel=\"noopener ugc\">MeinaUnreal - V4.1</a></p>\n<p>realistic: <a href=\"https://civitai.com/models/82984?modelVersionId=88171\" target=\"_blank\" rel=\"noopener ugc\">Paragon V1.0</a></p>\n<p>&nbsp;</p>\n<p>negative embeddings: <a href=\"https://civitai.com/models/71961/fast-negative-embedding-fastnegativev2\" target=\"_blank\" rel=\"noopener ugc\">Fast Negative Embedding v2</a></p>\n<p>&nbsp;</p>\n<p>Recommended settings:</p>\n<ul>\n<li>\n<p>Make sure you are aware on the usage instructions of LORA</p>\n</li>\n</ul>\n<ul>\n<li>\n<p>VAE: <a href=\"https://huggingface.co/stabilityai/sd-vae-ft-mse-original/blob/main/vae-ft-mse-840000-ema-pruned.safetensors\" target=\"_blank\" rel=\"noopener ugc\">vae-ft-mse-840000-ema-pruned</a> (for realistic models &amp; 2,5D), <a href=\"https://huggingface.co/hakurei/waifu-diffusion-v1-4/blob/main/vae/kl-f8-anime2.ckpt\" target=\"_blank\" rel=\"noopener ugc\">kl-f8-anime2</a>, <a href=\"https://huggingface.co/NoCrypt/blessed_vae/blob/main/blessed2.vae.pt\" target=\"_blank\" rel=\"noopener ugc\">blessed2.vae</a> (for anime models)</p>\n</li>\n</ul>\n<ul>\n<li>\n<p>Sampler: DPM++ SDE Karras or DPM++ 2M Karras(euler a for img2img upscaling) from time to time im also using <a href=\"https://civitai.com/models/35966/dpm-2m-alt-karras-sampler\" target=\"_blank\" rel=\"noopener ugc\">DPM++ 2M alt Karras</a></p>\n</li>\n<li>\n<p>Steps: 20 to 35</p>\n</li>\n<li>\n<p>Clipskip: 2</p>\n</li>\n<li>\n<p>Upscaler : <a href=\"https://github.com/pkuliyi2015/multidiffusion-upscaler-for-automatic1111\" target=\"_blank\" rel=\"noopener ugc\">GitHub - pkuliyi2015/multidiffusion-upscaler-for-automatic1111</a> <a href=\"https://mega.nz/folder/qZRBmaIY#nIG8KyWFcGNTuMX_XNbJ_g/file/vRYVhaDA\" target=\"_blank\" rel=\"noopener ugc\">4x-UltraSharp(for 2,5D &amp; realistic)</a>, <a href=\"https://huggingface.co/konohashinobi4/4xAnimesharp/tree/main\" target=\"_blank\" rel=\"noopener ugc\">4xAnimesharp(for anime)</a></p>\n</li>\n<li>\n<p>CFG Scale : 5 to 9 (mostly 7)</p>\n</li>\n<li>\n<p>LORA weight for txt2img: 0.5-1 (for the examples i mostly used 0.8 and 1)</p>\n</li>\n</ul>\n<ul>\n<li>\n<p>Denoise strength upscaling: 0.3 to 0.55</p>\n</li>\n</ul>\n<p>&nbsp;</p>\n<p>If you want to autocomplete the tags &amp; trigger im using, I recommend using <a href=\"https://github.com/DominikDoom/a1111-sd-webui-tagcomplete\" target=\"_blank\" rel=\"noopener ugc\">Booru style tag autocompletion for AUTOMATIC1111's Stable Diffusion web UI</a></p>\n<p>For full body shots i recommend using: <a href=\"https://civitai.com/models/33192/comfyui-impact-pack\" target=\"_blank\" rel=\"noopener ugc\">ComfyUI Impact Pack</a></p>\n<p>or the a1111 alternative <a href=\"https://github.com/Bing-su/adetailer\" target=\"_blank\" rel=\"noopener ugc\">GitHub - Bing-su/adetailer</a></p>\n</div>\n</div>\n</div>\n</div>",
    "screenshots": "https://cdn2.stablediffusionapi.com/generations/4077209311692874194.png"
