models.sort((a, b) => parseInt(b.api_calls) - parseInt(a.api_calls));

const groupedByCategory = {};

groupedByCategory['controlnet_type'] = controlNet_type;
groupedByCategory['styles'] = styles;

models.forEach(item => {
  const category = item.model_category;

  if (!groupedByCategory[category]) {
    groupedByCategory[category] = [];
  }

  groupedByCategory[category].push(item);
});

Object.keys(groupedByCategory).forEach(key => {
  groupedByCategory[key].sort((a, b) => parseInt(b.api_calls) - parseInt(a.api_calls));
});

// console.log(groupedByCategory)

function getBatchByIndex(category, index, limit) {
  const batchSize = limit;
  const start = index * batchSize;
  const end = start + batchSize;

  return groupedByCategory[category].slice(start, end);
}

export const model_list = function (category, offset, limit, callabck) {
  // console.log(category, offset)
  callabck(getBatchByIndex(category, offset - 1, limit), groupedByCategory[category].length);
}

export const model_list_other = function (category, index, limit, callabck) {
  let count = groupedByCategory[category].length - 1;
  index = Number(index);
  limit = Number(limit);

  let pages = [];

  if((index + limit) > count){
    for(let i = 0; i < limit; i++){
      pages.unshift(index - (Number(i) + 1));
    }
    callabck(groupedByCategory[category].slice(((index) - limit), (index)), pages);
  }else{
    index += 1;
    for(let i = 0; i < limit; i++){
      pages.push(index + i);
    }
    callabck(groupedByCategory[category].slice((index), ((index) + limit)), pages);
  }
}

export const getModel = function (category, index, callabck) {
  callabck(groupedByCategory[category][index]);
}