import React, { useState, useEffect } from 'react';

import $ from 'jquery';

function Score() {

    const [count, setCount] = useState(0);

    useEffect(() => {
        $.post('https://artpix.000webhostapp.com/score.php', function(data){
            data = JSON.parse(data);
            setCount(Number(data.score));
        });
    }, []);

    return (
        <div className="row d-flex justify-content-center align-items-center" style={{ flexDirection: 'column', width: '100%', height: '100px' }}>
            <div className="text-center">
                <h5>We've already generate {count.toLocaleString()} image</h5>
            </div>
        </div>
    );
}

export default Score;